export default "import React from 'react';\nimport DynamicGrid from 'terra-dynamic-grid';\nimport Card from './Card';\n\nconst template = {\n  'grid-template-columns': '1fr 1fr 1fr 1fr 1fr',\n  'grid-template-rows': '100px',\n  'grid-gap': '10px',\n};\n\nconst region1 = {\n  defaultPosition: {\n    'grid-column-start': 1,\n    'grid-row-start': 1,\n  },\n  small: {\n    'grid-column-start': 2,\n  },\n  medium: {\n    'grid-column-start': 3,\n  },\n  large: {\n    'grid-column-start': 4,\n  },\n};\n\nconst region2 = {\n  defaultPosition: {\n    'grid-column-start': 2,\n    'grid-row-start': 1,\n  },\n  small: {\n    'grid-column-start': 3,\n  },\n  medium: {\n    'grid-column-start': 4,\n  },\n  large: {\n    'grid-column-start': 5,\n  },\n};\n\n\nconst region3 = {\n  defaultPosition: {\n    'grid-column-start': 3,\n    'grid-row-start': 1,\n  },\n  small: {\n    'grid-column-start': 4,\n  },\n  medium: {\n    'grid-column-start': 5,\n  },\n  large: {\n    'grid-column-start': 1,\n  },\n};\n\nconst region4 = {\n  defaultPosition: {\n    'grid-column-start': 4,\n    'grid-row-start': 1,\n  },\n  small: {\n    'grid-column-start': 5,\n  },\n  medium: {\n    'grid-column-start': 1,\n  },\n  large: {\n    'grid-column-start': 2,\n  },\n};\n\nconst region5 = {\n  defaultPosition: {\n    'grid-column-start': 5,\n    'grid-row-start': 1,\n  },\n  small: {\n    'grid-column-start': 1,\n  },\n  medium: {\n    'grid-column-start': 2,\n  },\n  large: {\n    'grid-column-start': 3,\n  },\n};\n\n\nconst ResponsiveGrid = () => (\n  <DynamicGrid defaultTemplate={template}>\n    <DynamicGrid.Region {...region1}>\n      <Card>1</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region {...region2}>\n      <Card>2</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region {...region3}>\n      <Card>3</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region {...region4}>\n      <Card>4</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region {...region5}>\n      <Card>5</Card>\n    </DynamicGrid.Region>\n  </DynamicGrid>\n);\n\nexport default ResponsiveGrid;\n"