export default "import React from 'react';\nimport DynamicGrid from 'terra-dynamic-grid';\nimport Card from './Card';\n\nconst template = {\n  'grid-template-columns': '3fr 1fr 3fr',\n  'grid-template-rows': 'auto auto auto auto',\n};\n\nconst leftGutter = {\n  'grid-column-start': 1,\n  'grid-row-start': 1,\n  'grid-row-end': 5,\n};\n\nconst rightGutter = {\n  'grid-column-start': 3,\n  'grid-row-start': 1,\n  'grid-row-end': 5,\n};\n\nconst notification = {\n  'grid-column-start': 2,\n  'grid-column-end': 3,\n  'grid-row-start': 1,\n};\n\nconst region1 = {\n  'grid-column-start': 2,\n  'grid-row-start': 2,\n  style: {\n    padding: '0 5px 5px 5px',\n  },\n};\n\nconst region2 = {\n  'grid-column-start': 2,\n  'grid-row-start': 3,\n  style: {\n    padding: '5px',\n  },\n};\n\nconst region3 = {\n  name: 'r3',\n  'grid-column-start': 2,\n  'grid-row-start': 4,\n  style: {\n    padding: '5px 5px 0',\n  },\n};\n\n\nconst DashboardLayout = () => (\n  <DynamicGrid defaultTemplate={template}>\n    <DynamicGrid.Region defaultPosition={leftGutter}>\n      <Card>left gutter</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region defaultPosition={notification}>\n      <Card>notification</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region defaultPosition={rightGutter}>\n      <Card>right gutter</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region defaultPosition={region1}>\n      <Card>r1</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region defaultPosition={region2}>\n      <Card>r2</Card>\n    </DynamicGrid.Region>\n    <DynamicGrid.Region defaultPosition={region3}>\n      <Card>r3</Card>\n    </DynamicGrid.Region>\n  </DynamicGrid>\n);\n\nexport default DashboardLayout;\n"